<template>
    <yd-layout :class="' fillpost'">
        <yd-navbar bgcolor="#70b6ef" title="中国银联签报" color="#fff" fontsize="0.36rem">
            <router-link to="/" slot="left">
                <yd-navbar-back-icon color="#fff">返回</yd-navbar-back-icon>
            </router-link>
        </yd-navbar>
        <div class="schedules fillPost" :class="{isSecretary: isSecretary == false}">
            <yd-tab :callback="tabFun">
                <yd-tab-panel label="普通发送" active size=".32rem">
                    <div class="fuyan" v-if="liuyanFlag">
                        <yd-flexbox direction="vertical">
                            <yd-flexbox-item>
                                <yd-flexbox :class="'goTop'">
                                    <div class="range assistRange nowrap">上一步附言：</div>
                                    <div class="office" v-html="liuyan"></div>
                                </yd-flexbox>
                            </yd-flexbox-item>
                        </yd-flexbox>
                    </div>
                    <div class="fuyan_box">
                        <div class="adviceTxt"  v-if="!threeFlag && !toggleFlag">
                            <yd-flexbox :class="'padd33'">
                                <yd-flexbox-item>
                                    <div>
                                        <span class="yijian" >填写附言：</span>
                                        <trsTextarea :trsType="true" id="js_writefuyan" placeholder="请输入附言内容..." v-model="opinion"></trsTextarea>
                                    </div>
                                </yd-flexbox-item>
                            </yd-flexbox>
                        </div>
                        <div class="adviceTxt1"  v-else>
                            <yd-flexbox direction="vertical">
                                <div class="mainContentFluid bor5">
                                    <div class="advice">
                                        <div class="commonAdvice">
                                            常用意见：
                                        </div>
                                    </div>
                                    <div class="commonopinion">
                                        <span :class='{active: opinionIndex == index}' v-for="(item, index) in opinionArr" @click="opinion3 = item; opinionIndex = index" v-text="item"></span>
                                    </div>
                                </div>
                                <div class="mainContentFluid bor5 paR">
                                    <div class="adviceTxt">
                                        <div class="commonAdvice">意见内容：</div>
                                        <trsTextarea :trsType="true" v-model="opinion3" placeholder="请输入您的意见"></trsTextarea>
                                    </div>
                                </div>
                            </yd-flexbox>
                        </div>
                    </div>
                    <div class="mainContentFluid" id="js_selectpeople">
                        <div class=" agent1" v-bind:class="{agent1Width: isAll == false && officeActive1==1}">
                            <yd-flexbox direction="vertical">
                                <yd-flexbox-item>
                                    <div class="office 11 " v-bind:class="{active:officeActive1==1}" v-on:click="officeActive1=1;sendFlag = true;" v-text="sendType"></div>
                                </yd-flexbox-item>
                                <yd-flexbox-item v-if="threeFlag && !querytwoFlag" :class='{twoBox:threeFlag==true}'>
                                    <div class="office 12" v-bind:class="{active:officeActive1==3}" v-on:click="officeActive1=3;sendFlag = true;" v-text="sendType1"></div>
                                </yd-flexbox-item>
                                <yd-flexbox-item v-if="threeFlag && querytwoFlag" :class='{twoBox:threeFlag==true}'>
                                    <div class="office 13" v-bind:class="{active:officeActive1==3}" v-on:click="officeActive1=3, statusPopup04 = true;leaderName4.length > 0 ? sendFlag = true: sendFlag = false">{{cosnextName1  + leaderName4 }}</div>
                                </yd-flexbox-item>
                                <yd-flexbox-item v-if="dobleFlag">
                                    <div class="office 22" v-if="!cosnextFlag" v-bind:class="{active:officeActive1==2}" v-on:click="officeActive1=2;statusPopup01 = oneNameFlag == true && isSecretary == true ? false : true;leaderName3.length > 0 ? sendFlag = true: sendFlag = false;">{{lingdaoName + leaderName3 }}</div>
                                    <div class="office 33" v-else v-bind:class="{active:officeActive1==2}" v-on:click="officeActive1=2;statusPopup01 = oneNameFlag == true && isSecretary == true ? false : true;leaderName3.length > 0 ? sendFlag = true: sendFlag = false">{{cosnextName + leaderName3 + ''}}</div>
                                </yd-flexbox-item>
                            </yd-flexbox>
                        </div>
                    </div>
                </yd-tab-panel>
                <yd-tab-panel :class="'bgE'" label="代领导录入">
                    <div class="mainContentFluid bor5" id="js_selectlingdao">
                        <div class="advice">
                            <div class="commonAdvice">
                                <span>选择领导：</span><span v-for="(item, index) in leaderNameArr" v-text="item.empname"></span>
                            </div>
                        </div>
                    </div>
                    <div class="mainContentFluid bor5" id="js_changyong_tolead">
                        <div class="advice">
                            <div class="commonAdvice">
                                常用意见：
                            </div>
                        </div>
                        <div class="commonopinion">
                            <span :class='{active: opinionIndex == index}' v-for="(item, index) in opinionArr" @click="opinion2 = item; opinionIndex = index" v-text="item"></span>
                        </div>
                    </div>
                    <div class="mainContentFluid bor5" id="js_pishi_tolead">
                        <div class="adviceTxt">
                            <div class="commonAdvice">批示意见：</div>
                            <trsTextarea :trsType="true" v-model="opinion2" placeholder="请输入您的意见" :value="isAgree2"></trsTextarea>
                        </div>
                    </div>
                    <div class="blank"></div>
                    <div class="mainContentFluid" id="js_selectpeople_tolead">
                        <div class=" agent1">
                            <yd-flexbox direction="vertical">
                                <yd-flexbox-item>
                                    <div class="office" v-bind:class="{active:officeActive2==1}" v-on:click="officeActive2=1; sendFlag = true;" v-text="sendType">
                                    </div>
                                </yd-flexbox-item>
                                <yd-flexbox-item>
                                    <div v-if="leaderFlag" class="office 11" v-bind:class="{active:officeActive2==2}" v-on:click="officeActive2=2;statusPopup01 = oneNameFlag == true && isSecretary == true ? false : true;leaderName3.length > 0 ? sendFlag = true: sendFlag = false">{{'送领导审批' + leaderName3 + ''}}</div>
                                    <div v-else class="office 22" v-bind:class="{active:officeActive2==2}" v-on:click="officeActive2=2; sendFlag = true;">{{'送领导审批' + leaderName3 + ''}}</div>
                                </yd-flexbox-item>
                            </yd-flexbox>
                        </div>
                    </div>
                </yd-tab-panel>
            </yd-tab>
        </div>
        <yd-tabbar v-show="!statusPopup01" class="bottom" slot="tabbar" bgcolor="#eee" color="#666">
            <div v-bind="{ id : tabKey == '代领导录入' ? 'js_fanhui_nor' : 'js_fanhui_tolead' }" class="right_btn toTop" @click="toBack(2)">
                返回
            </div>
            <div id="js_surefasong_tolead" class="left_btn toTop" v-if="tabKey == '代领导录入'"  @click="sendMess1">
                确认发送
            </div>
            <div id="js_surefasong" class="left_btn toTop" v-else  @click="sendMess">
                确认发送
            </div>
        </yd-tabbar>
        <!-- 选择领导 -->
        <yd-popup v-model="statusPopup01" v-show="statusPopup01" position="bottom" height="4rem">
            <div slot="top" class="confirmBtn" >
                <div class="leftBtn" v-on:click="statusPopup01 = false">取消</div>
                <div class="rightBtn" v-on:click="statusPopup01 = false;selectLeader()">确定</div>
            </div>
            <div class="selectStatus selectLeader1 abc">
                <ul class="ul" v-if="isSecretary && !oneNameFlag">
                    <li class="li" v-bind:class="{active:statusRadio[index]%2==1}" v-for="(status,index) in leaderNameArr" v-on:click="addLeader(status, index)" v-text="status.empname"></li>
                </ul>
                <ul class="ul" v-else>
                    <li class="li" v-bind:class="{active:statusRadioa==index}" v-for="(status,index) in leaderNameArr" v-on:click="statusRadioa=index;leaderName3 = '('+status.empname+ ')';leadersId = status.operatorid;sendFlag = true;" v-text="status.empname"></li>
                </ul>
            </div>
        </yd-popup>
        <!-- 代领导录入常用意见 -->
        <yd-popup v-model="statusPopup02" v-show="statusPopup02" position="bottom" height="4rem">
            <div slot="top" class="confirmBtn">
                <div class="leftBtn" v-on:click="statusPopup02 = false">取消</div>
                <div class="rightBtn" v-on:click="statusPopup02 = false;selectLeader3()">确定</div>
            </div>
            <div class="selectStatus adviceStatus">
                <ul class="ul">
                    <li class="li" v-bind:class="{active:statusRadio2===index}" v-for="(status,index) in agreeOpinion" v-on:click="opinion2=status;statusRadio2=index;" v-text="status"></li>
                </ul>
            </div>
        </yd-popup>
        <!-- 代领导录入选择领导 -->
        <yd-popup v-model="statusPopup03" v-show="statusPopup03" position="bottom" height="4rem">
            <div slot="top" class="confirmBtn">
                <div class="leftBtn" v-on:click="statusPopup03 = false">取消</div>
                <div class="rightBtn" v-on:click="statusPopup03 = false;">确定</div>
            </div>
            <div class="selectStatus selectLeader2">
                <ul class="ul">
                    <li class="li" v-bind:class="{active:statusRadio3===index}" v-for="(status,index) in leaderNameArr" v-on:click="isAgree2=status.empname;statusRadio3=index;" v-text="status.empname"></li>
                </ul>
            </div>
        </yd-popup>
        <yd-popup v-model="statusPopup04" v-show="statusPopup04" position="bottom" height="4rem">
            <div slot="top" class="confirmBtn">
                <div class="leftBtn" v-on:click="statusPopup04 = false">取消</div>
                <div class="rightBtn" v-on:click="statusPopup04 = false;selectLingdao4()">确定</div>
            </div>
            <div class="selectStatus selectLeader2">
                <ul class="ul">
                    <li class="li" v-bind:class="{active:statusRadio4===index}" v-for="(status,index) in leaderNameArr2" v-on:click="statusRadio4=index;leaderName4 = ' (' + status.empname + ')';deparmentId1 = status.operatorid" v-text="status.empname"></li>
                </ul>
            </div>
        </yd-popup>
    </yd-layout>
</template>
<script type="text/babel">
import $ from '../../jquery-vendor.js';
import { mapGetters } from 'vuex';
export default {
    name: 'fillPostscript',
    props: ['title'],
    data() {
        return {
            tabKey: -1,
            opinionIndex: -1,
            opinionIndex2: -1,
            opinion_index: 0,
            opinionArr: [],
            statusRadio: false,
            statusRadioa: -1,
            statusRadio2: false,
            statusRadio3: false,
            statusRadio33: false,
            statusRadio4: false, // 发文时候是否有两个queryHandlerPerson
            statusTwo: false,
            statusTwo2: false,
            statusTwo3: false,
            statusTwo4: false,
            statusPopup01: false,
            statusPopup02: false,
            officeActive1: 1,
            officeActive2: 1,
            statusPopup03: false,
            statusPopup033: false,
            statusPopup04: false,
            isSecretary: false, // 是否为秘书
            isAll: true, // 是否为总公司
            leaderFlag: false, // 代领导录入是否需要选择领导
            opinion: '',
            opinion2: '', // 代领导录入页面的意见内容
            opinion3: '', // 意见内容
            agreeArr: [],
            agreeOpinion: [],
            isAgree: '',
            isAgree2: '请选择',
            leaderName: '',
            leaderName2: '',
            leaderName3: '',
            leaderName4: '', // 发文时候是否有两个queryHandlerPerson
            leaderName5: '', // 不需要括号的领导名字
            deparment: '',
            deparmentId: '',
            threeFlag: false, // nxtBTN是否有三个
            querytwoFlag: false, // 发文时候是否有两个queryHandlerPerson
            dobleFlag: true, // nxtBTN俩个的时候需要显示意见而不是附言
            deparment1: '', // 第二个部门
            deparmentId1: '',
            isApprove: false,
            leaderTypeArr: [],
            oneNameFlag: false, // 只有一个领导
            leaderTypeArr2: [], // 发文时候是否有两个queryHandlerPerson
            cosnextName1: '', // 发文时候是否有两个queryHandlerPerson
            leaderNameArr: [], // 普通发送的领导
            leaderNameArr2: [], // 发文时候是否有两个queryHandlerPerson
            addLeaderArr: [], // 多个领导
            addLeaderIdArr: [], // 多个领导id
            leadersId: 0, // 普通发送领导id
            toggleFlag: false,
            sendType: '',
            sendType1: '',
            nextId: '',
            nextId1: '',
            cosnextId: '',
            cosnextName: '', // 领导下一环节环节名称
            liuyan: '',
            liuyanFlag: false,
            lingdaoName: '送领导审批',
            sendFlag: true, // 没有选择人员不能发送不能发送
            cosnextFlag: false // 领导是否显示下一环节名称
        }
    },
    computed: {
        ...mapGetters({
            userInfo: 'getUserInfo',
            handleInfo: 'getHandleInfo',
            titleName: 'getTitleName'
        })
    },
    beforeRouteLeave(to, from, next) {
        // 设置下一个路由的 meta
        to.meta.keepAlive = true; // C 跳转到 A 时让 A 不缓存，即刷新
        next();
    },
    mounted() {
        var vm = this;
        console.log(vm.title);
        switch (this.titleName) {
            case '中国银联签报':
                this.opinionArr = ['同意', '不同意', '退回修改'];
                break;
            case '中国银联发文':
                this.opinionArr = ['同意', '不同意', '退回修改'];
                break;
            case '中国银联收文':
                this.opinionArr = ['已阅', '已阅，尽快落实'];
                break;
        }
        vm.pageRender();
        document.setTitle(this.titleName);
    },
    methods: {
        pageRender() {
            var vm = this;
            // 签报、发文类型的流程，一个秘书就一个领导的，不需要选择 收文的还是需要选择的
            if (vm.titleName == '中国银联签报' || vm.titleName == '中国银联收文') {
                vm.leaderFlag = false;
            } else {
                vm.leaderFlag = true;
            }
            var sendQuery = 0; // eslint-disable-line no-unused-vars
            // 处理nextBtn
            // 下一步id
            // 发送审批判断
            for (let key in vm.handleInfo.nextBtn) {
                if (key == 'doApprove') {
                    vm.isApprove = true;
                }
            }
            if (vm.isApprove) {
                // 当有doApprove这个key的时候需要通过leaderTypeArr获取所有领导的名字
                var approveArr = [];
                var noapproveArr = [];
                for (let keyJ in vm.handleInfo.nextBtn) {
                    approveArr.push(keyJ);
                }
                for (let j = 0; j < approveArr.length; j++) {
                    if (approveArr[j] == 'doApprove') {
                        if (vm.handleInfo.doApprove != 'toggle') {
                            vm.approveNum = vm.handleInfo.nextBtn.doApprove.type;
                            vm.leaderTypeArr = vm.handleInfo.nextBtn.doApprove.queryHandlerPerson.split(','); // 获取领导接口的type
                        } else {
                            // doApprove: {1015946: "温泉", type: "2"}, fwDengjifawenAct: ["1014635", "吴媚"] 发文和收文的审批跳发送的页面
                            vm.toggleFlag = true;
                            vm.approveNum = vm.handleInfo.nextBtn.doApprove.type;
                            for (let o in vm.handleInfo.nextBtn.doApprove) {
                                if (o != 'type') {
                                    vm.lingdaoName = vm.handleInfo.activityDefId == 'fwFawenhegaoAct' ? '返回办公室核稿' : '返回送审人';
                                    // vm.officeActive1 = vm.handleInfo.activityDefId == 'fwFawenhegaoAct' ? 2 : 1;
                                    vm.leadersId = o; // 领导id
                                    vm.leaderName3 = vm.handleInfo.nextBtn.doApprove[o];
                                    vm.leaderNameArr = [{ 'empname': vm.leaderName3, 'operatorid': vm.leadersId }];
                                    vm.leaderName3 = ' (' + vm.handleInfo.nextBtn.doApprove[o] + ')';
                                    vm.oneNameFlag = true;
                                }
                            }
                        }
                    } else {
                        if (approveArr.length == 2) {
                            vm.nextId = approveArr[j]; // 下一步流程ID
                            vm.deparment = vm.handleInfo.nextBtn[approveArr[j]][1];
                            vm.deparmentId = vm.handleInfo.nextBtn[approveArr[j]][0]; // 部门ID
                        } else if (approveArr.length == 3) {
                            noapproveArr.push(approveArr[j]);
                            // doApprove:{queryHandlerPerson: "25", type: "0"},fwDengjifawenAct:["1014635", "吴媚"],fwFawenhegaoAct:["1014632", "夏军"]
                            // console.log(noapproveArr);
                        }
                    }
                }
                // doApprove:{queryHandlerPerson: "25", type: "0"},fwDengjifawenAct:["1014635", "吴媚"],fwFawenhegaoAct:["queryHandlerPerson", "2"]
                if (approveArr.length == 3) {
                    for (let e = 0; e < noapproveArr.length; e++) {
                        if (vm.handleInfo.nextBtn[noapproveArr[e]][0] == 'queryHandlerPerson') {
                            vm.querytwoFlag = true;
                        }
                    }
                    vm.threeFlag = true;
                    if (!vm.querytwoFlag) {
                        vm.nextId = noapproveArr[0]; // 下一步流程ID
                        vm.deparment = vm.handleInfo.nextBtn[noapproveArr[0]][1];
                        vm.deparmentId = vm.handleInfo.nextBtn[noapproveArr[0]][0]; // 部门ID
                        vm.nextId1 = noapproveArr[1]; // 部门2下一步id
                        vm.deparment1 = vm.handleInfo.nextBtn[noapproveArr[1]][1]; // 部门2或者人物名称
                        vm.deparmentId1 = vm.handleInfo.nextBtn[noapproveArr[1]][0]; // 部门2或者人物Id
                    } else {
                        for (let e = 0; e < noapproveArr.length; e++) {
                            if (vm.handleInfo.nextBtn[noapproveArr[e]][0] == 'queryHandlerPerson') {
                                 // 部门2下一步id
                                vm.nextId1 = noapproveArr[e];
                                if (noapproveArr[e] == 'fwQicaofawenAct') {
                                    vm.cosnextName1 = '主办方-发文审核';
                                } else if (noapproveArr[e] == 'fwFawenhegaoAct') {
                                    vm.cosnextName1 = '文件审核人-发文核稿';
                                } else if (noapproveArr[e] == 'fwFawenfuheAct') {
                                    vm.cosnextName1 = '文件复核人-发文复核';
                                }
                                vm.leaderTypeArr2 = vm.handleInfo.nextBtn[noapproveArr[e]][1].split(',');
                                for (let i = 0; i < vm.leaderTypeArr2.length; i++) {
                                    vm.get(vm.conf.portalMobile + '/API/WorkFlow/oawf/handlerPerson', {
                                        'type': vm.$route.params.type,
                                        'token': `${vm.userInfo.ssotoken}L/wp-2q${vm.userInfo.ssotokenCode}`,
                                        'queryType': vm.leaderTypeArr2[i]
                                    }, function(res) {
                                        var data = res.jsonData;
                                        if (data.length == 1) {
                                            vm.oneNameFlag = true;
                                        }
                                        if (data == null || data == undefined || data == '' || data == 'undefined') {
                                        } else {
                                            vm.leaderNameArr = vm.leaderNameArr.concat(data);
                                        }
                                    });
                                }
                            } else {
                                vm.nextId = noapproveArr[e]; // 部门2下一步id
                                vm.deparment = vm.handleInfo.nextBtn[noapproveArr[e]][1]; // 部门2或者人物名称
                                vm.deparmentId = vm.handleInfo.nextBtn[noapproveArr[e]][0]; // 部门2或者人物Id
                            }
                        }
                    }
                }
                // 送领导审批 的领导名字
                var promise = new Promise(function (resolve, reject) {
                    for (let i = 0; i < vm.leaderTypeArr.length; i++) {
                        vm.get(vm.conf.portalMobile + '/API/WorkFlow/oawf/handlerPerson', {
                            'type': vm.$route.params.type,
                            'token': `${vm.userInfo.ssotoken}L/wp-2q${vm.userInfo.ssotokenCode}`,
                            'queryType': vm.leaderTypeArr[i]
                        }, function(res) {
                            var data = res.jsonData;
                            if (data.length == 1) {
                                vm.oneNameFlag = true;
                            }
                            if (data == null || data == undefined || data == '' || data == 'undefined') {
                            } else {
                                vm.leaderNameArr = vm.leaderNameArr.concat(data);
                            }
                            if (i == vm.leaderTypeArr.length - 1) {
                                resolve()
                            }
                        });
                    }
                });
                promise.then(function () {
                });
            } else {
                // 分公司只有一个key的时候wjrQianbaoDjActivity:['1016737', '宁波分公司']
                // 没有doapprove的时候循环key 把key放到一个数组里面然后取第一个为部门，第二个为领导
                let keyArr = [];
                for (let keyK in vm.handleInfo.nextBtn) {
                    keyArr.push(keyK);
                }
                if (keyArr.length == 2) {
                    // 判断是否显示常用意见fwFawenfuheAct、swnb_activity、wjsh_activity判断有没有这三个id
                    if (keyArr[0] == 'swnb_activity' || keyArr[0] == 'wjsh_activity' || keyArr[0] == 'wFawenfuheAct' || keyArr[1] == 'swnb_activity' || keyArr[1] == 'wjsh_activity' || keyArr[1] == 'wFawenfuheAct') {
                        vm.dobleFlag = false;
                        vm.threeFlag = true;
                        // 办公室核稿，发送下一环节的默认需要换一下；默认选中吴媚
                        if (keyArr[1] == 'swfb_activity') {
                            keyArr.reverse();
                        }
                        vm.nextId = keyArr[0]; // 部门下一步id
                        vm.deparment = vm.handleInfo.nextBtn[keyArr[0]][1]; // 部门或者人物名称
                        vm.deparmentId = vm.handleInfo.nextBtn[keyArr[0]][0]; // 部门或者人物Id
                        vm.nextId1 = keyArr[1]; // 部门2下一步id
                        vm.deparment1 = vm.handleInfo.nextBtn[keyArr[1]][1]; // 部门2或者人物名称
                        vm.deparmentId1 = vm.handleInfo.nextBtn[keyArr[1]][0]; // 部门2或者人物Id
                    } else {
                        // json解析的时候顺序会不会变确保第一个为部门，第二个为领导
                        if (vm.handleInfo.nextBtn[keyArr[0]][0] == 'queryHandlerPerson') {
                            keyArr = keyArr.reverse();
                        }
                        if (vm.handleInfo.nextBtn[keyArr[1]][0] == 'queryHandlerPerson') {
                            vm.nextId = keyArr[0]; // 部门下一步id
                            vm.deparment = vm.handleInfo.nextBtn[keyArr[0]][1]; // 部门或者人物名称
                            vm.deparmentId = vm.handleInfo.nextBtn[keyArr[0]][0]; // 部门或者人物Id
                            vm.cosnextId = keyArr[1]; // 领导下一步id
                            vm.leaderTypeArr = vm.handleInfo.nextBtn[keyArr[1]][1].split(','); // 获取领导接口的type
                            // 送领导审批 的领导名字
                            var promise1 = new Promise(function (resolve, reject) {
